import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

const SpeedControlContainer = styled.div`
	position: relative;
	top: 60px;
	margin-bottom: -18px;
`;

const RangeInput = styled.input`
	direction: rtl; // Puts min is on right and max on left to properly work w/ interval
	-webkit-appearance: none;
	appearance: none;
	background-color: white;
	&::-webkit-slider-runnable-track {
		height: 1px;
	}
	&::-webkit-slider-thumb {
		-webkit-appearance: none;
		appearance: none;
		background: #7085ff;
		border: 1px solid white;
		border-radius: 50%;
		width: 15px;
		height: 15px;
		margin-top: -8px;
		cursor: pointer;
	}
`;

const RangeLabel = styled.label`
	color: white;
	position: relative;
	top: 6px;
	right: 5px;
	font-size: 0.75em;
`;

const SpeedControl = ({ speed, setSpeed }) => {
	return (
		<SpeedControlContainer>
			<RangeLabel>SPEED</RangeLabel>
			<RangeInput type='range' min='0' max='10' value={speed} onChange={({ target }) => setSpeed(target.value)} />
		</SpeedControlContainer>
	);
};

const Marquee = () => {
	const [position, setPosition] = useState(typeof window !== 'undefined' ? window.innerWidth : 0);
	const [content, setContent] = useState(`Welcome! Below you'll find a directory of my profiles and live projects.`);
	const [speed, setSpeed] = useState(5);

	const fetchContent = async () => {
		const gistUrl = 'https://api.github.com/gists/ce0eb2a14fd6e6b98f3e82fc7b82e656';
		const gist = await fetch(gistUrl).then((res) => res.json());
		const gistContent = gist?.files['personal-site-content']?.content;
		if (gistContent) {
			const { scrollingSkyText } = JSON.parse(gistContent);
			setContent(scrollingSkyText);
		}
	};

	useEffect(() => {
		// fetchContent();
	}, []);

	useEffect(() => {
		const interval = setInterval(() => {
			setPosition((prevPos) => {
				const newPos = prevPos - 1;
				if (newPos < -content.length * 1.75 * 10) {
					return window.innerWidth;
				}
				return newPos;
			});
		}, speed);
		return () => clearInterval(interval);
	}, [speed]);

	return (
		<div>
			<div
				style={{
					position: 'absolute',
					whiteSpace: 'nowrap',
					left: position,
					color: 'white',
					marginTop: '32px',
				}}
			>
				{content}
			</div>
			<SpeedControl speed={speed} setSpeed={setSpeed} />
		</div>
	);
};

export default Marquee;
